function settings() {

}

settings.prototype.on = function() {
    var wins = dhxWindows.getInstance('settingstWins', 'Settings');
    wins.button('minmax').hide();
    wins.button('park').hide();
    wins.denyResize();
    wins.showInnerScroll();

    $.get(apps.contextPath + '/js/tmpl/window-submenu.html', function (response) {
        wins.attachHTMLString(response);
    });
}; 
