function board() {

}

board.prototype.on = function(){
    var wins = dhxWindows.getInstance('boardWins', 'Board');

    var toolbar = wins.attachToolbar({
        icon_path: apps.contextPath + '/assets/dhtmlx/dhtmlxSutie/skins/material/dhxtoolbar_material/'
    });
    toolbar.loadStruct(this.getStruct().structCommon().grid_toolbar);

    var grid = wins.attachGrid();
    grid.setImagePath('https://dhtmlx.com/docs/products/codebase/imgs/dhxgrid_material/');
    grid.setHeader('Sales, Book Title, Author');
    grid.setInitWidths('70,250,*');
    grid.setColAlign('right,left,left');
    grid.setColTypes('dyn,ed,ed');
    grid.setColSorting('int,str,str');
    grid.init();
    grid.parse(this.getMock().getGrid(), 'json');
}

