function webos(_services, _callback) {
    window.dhxWindows = new dhtmlxWindowsPlugin();

    window.addEventListener('keydown', function (e) {
        if (e.keyCode === 27) {
            dhxWindows.closeOnTopWindow();
        }
    }, true);

    window.addEventListener('resize', function () {
        dhxWindows.resizeViewport();
    });

    $.get('/demo/js/tmpl/webos-tmpl.html', function(response){
        $.tmpl(response, _services).appendTo("#webos_container");
        //$(".container").html(response);
        if (_callback){
            _callback();
        }
    });
}

webos.prototype.container = function() {

}

webos.prototype.taskbar = function () {

}

webos.prototype.logout = function () {
    $('#start').trigger('click');
    alert('logout');
}

